<template>
  <div id="app">
    <div>
      <h2>Vue.js Tree Plugin</h2>
      <p>if you feel it useful or like it please give a star @ <a href="https://github.com/anderlaw/vue-tree-plugin">Github</a> Thanks</p>
      <div style="display:flex;">
        <textarea
          @change="handleDefaultTreeChange"
          cols="70"
          rows="30"
          v-model="defaultTreeDataCode"
        ></textarea>
        <vue-tree-plugin
          :data="defaultTreeData"
          @onNodeClick="handleSelect"
          @onModifyBtnClick="handleModify"
          @onAppendBtnClick="handleAppend"
          @onDeleteBtnClick="handleDefaultDelete"
        />
      </div>
    </div>
    <!-- 新建提示 -->
    <div class="tipbox" v-if="showAppendBox">
      <div class="close" @click="showAppendBox=false;">X</div>
      <h4 class="title">modify node information</h4>
      <div class="form-item">
        <input type="text" v-model="newNodeName" placeholder="new node name" />
      </div>
      <div class="form-item">
        <input type="text" v-model="newNodeDesc" placeholder="new node description" />
      </div>
      <div class="form-item">
        <button @click="updateNode">Ok</button>
      </div>
    </div>
  </div>
</template>

<script>
import HelloWorld from "./components/HelloWorld.vue";

export default {
  name: "App",
  data() {
    return {
      defaultTreeDataCode: JSON.stringify(
        [
          {
            iconClass: "iconfont icon-layout",
            name: "node1",
            desc: ".node1",
            isLeaf: false,
            children: [
              {
                iconClass: "iconfont icon-layout",
                name: "node1-1",
                desc: ".node1-1",
                isLeaf: false,
                children: [
                  {
                    iconClass: "iconfont icon-picture",
                    name: "node1-1-1",
                    desc: ".node1-1-1",
                    isLeaf: true,
                  },
                  {
                    iconClass: "iconfont icon-text",
                    name: "node1-1-2",
                    desc: ".node1-1-2",
                    isLeaf: true,
                  },
                ],
              },
              { name: "node2", desc: ".node2", isLeaf: false },
            ],
          },
        ],
        "",
        4
      ),
      defaultTreeData: [
        {
          iconClass: "iconfont icon-layout",
          name: "node1",
          desc: ".node1",
          isLeaf: false,
          children: [
            {
              iconClass: "iconfont icon-layout",
              name: "node1-1",
              desc: ".node1-1",
              isLeaf: false,
              children: [
                {
                  iconClass: "iconfont icon-picture",
                  name: "node1-1-1",
                  desc: ".node1-1-1",
                  isLeaf: true,
                },
                {
                  iconClass: "iconfont icon-text",
                  name: "node1-1-2",
                  desc: ".node1-1-2",
                  isLeaf: true,
                },
              ],
            },
            { name: "node2", desc: ".node2", isLeaf: false },
          ],
        },
      ],
      newNodeIndex: 360,
      showAppendBox: false,
      currentPos:"",
      newNodeName:"",
      newNodeDesc:"",
    };
  },
  methods: {
    handleDefaultTreeChange(event) {
      // console.log(code);
      this.defaultTreeData = eval(event.target.value);
    },
    handleSelect(pos) {
      alert("you have click at:" + pos);
    },
    handleModify(pos) {
      this.currentPos = pos
      this.showAppendBox = true;
      this.newNodeName = this.newNodeDesc = "";
    },
    updateNode(){
      this.showAppendBox = false;
      const arr = this.currentPos.split("-");
      const copyData = JSON.parse(JSON.stringify(this.defaultTreeData));

      if (arr.length === 1) {
        copyData[arr[0]].name = this.newNodeName
        copyData[arr[0]].desc = this.newNodeDesc
      } else {
        const node = arr.reduce((iter, number, index) => {
          if (index === 0) {
            return iter[number];
          } else {
            return iter.children[number];
          }
        }, copyData);
        node.name = this.newNodeName
        node.desc = this.newNodeDesc
      }
      this.defaultTreeData = copyData;

      this.defaultTreeDataCode = JSON.stringify(copyData, "", 4);
    },
    handleDefaultDelete(pos) {
      const arr = pos.split("-");
      const copyData = JSON.parse(JSON.stringify(this.defaultTreeData));

      if (arr.length === 1) {
        copyData.splice(arr[0], 1);
      } else {
        const parentNode = arr
          .slice(0, arr.length - 1)
          .reduce((iter, number, index) => {
            if (index === 0) {
              return iter[number];
            } else {
              return iter.children[number];
            }
          }, copyData);
        parentNode.children.splice(arr[arr.length - 1], 1);
      }
      this.defaultTreeData = copyData;

      this.defaultTreeDataCode = JSON.stringify(copyData, "", 4);
    },
    handleAppend(pos) {
      const arr = pos.split("-");
      const copyData = JSON.parse(JSON.stringify(this.defaultTreeData));

      if (arr.length === 1) {
        !copyData[arr[0]].children && (copyData[arr[0]].children = []);
        copyData[arr[0]].children.push({
          name: "randomNode" + this.newNodeIndex++,
          desc: "too much random!",
        });
      } else {
        const node = arr.reduce((iter, number, index) => {
          if (index === 0) {
            return iter[number];
          } else {
            return iter.children[number];
          }
        }, copyData);
        !node.children && (node.children = []);
        node.children.push({
          name: "randomNode" + this.newNodeIndex++,
          desc: "too much random!",
        });
      }
      this.defaultTreeData = copyData;

      this.defaultTreeDataCode = JSON.stringify(copyData, "", 4);
    },
  },
};
</script>

